import * as React from "react"
import { Link, graphql } from "gatsby"

import Bio from "../components/bio"
import Layout from "../components/layout"
import Seo from "../components/seo"

const handleClick = () => {
  window.location.href = "https://www.youtube.com"; // 유튜브 링크로 이동
};

const FacebookButton = () => {
  return (
    <button className="facebook-button" onClick={handleClick} style={{ width: "100px", height: "50px" }}>
      <span className="facebook-text">주문하기</span>
    </button>
  );
};

const BlogIndex = ({ data, location }) => {
  const siteTitle = data.site.siteMetadata?.title || `Title`
  const post = data.markdownRemark

  return (
    <Layout location={location} title={siteTitle}>
      <Bio />

      <div style={{ display: "flex", justifyContent: "center" }}>
        <FacebookButton />
      </div>

      <section
        dangerouslySetInnerHTML={{ __html: post.html }}
        itemProp="articleBody"
      />
    </Layout>
  )
}

export default BlogIndex

export const Head = () => <Seo title="All posts" />

export const pageQuery = graphql`
  {
    site {
      siteMetadata {
        title
      }
    }
    markdownRemark(fields: { slug: { eq: "/landing-page/" } }) {
      html
    }
  }
`
